.avatar {
  position: relative;
  &__img {
    border-radius: 50%;
  }
  &__circle {
    position: absolute;
    top: 0;
    right: 10px;
    background-color: antiquewhite;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    text-align: center;
    color: rgb(29, 53, 31);
  }
}
